.App {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: all 0.25s;
}

.bgbox {
    z-index: -3;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    filter: blur(6px);
    object-fit: cover;
    transition: opacity 1s, transform 0.25s;
}
.bgFocus {
    filter: blur(6px) !important;
    transform: scale(1.1);
}

.cover {
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: radial-gradient(
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.5) 100%
        ),
        radial-gradient(rgba(0, 0, 0, 0) 33%, rgba(0, 0, 0, 0.3) 166%);
    transition: all 0.25s;
}

.template {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10000;
    background: #00000030;
    color: #fff;
    padding: 6px 10px;
    font-size: 14px;
    .btn {
        margin-left: 8px;
        cursor: pointer;
        color: #aaa;
        text-decoration: underline;
    }
    .success {
        color: var(--theme-color);
    }
}

.navBox {
    position: absolute;
    top: 280px;
    left: calc(10% - 14px);
    width: 462px;
    z-index: 2;
    // min-width: 330px;
}

.yiyanBox {
    position: absolute;
    z-index: 1;
    bottom: 10%;
    width: 80%;
    left: 50%;
    transform: translate(-50%);
}

.configContainer {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    right: 20px;
}

.videoBg {
    z-index: -2;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 1s, transform 0.25s, filter 0.25s;
}
.login {
    position: absolute;
    top: 6px;
    left: 6px;
    &:hover {
        cursor: pointer;
    }
}

.footer {
    position: absolute;
    bottom: 6px;
    width: 100%;
    text-align: center;
    
    .beian {
        font-size: 12px;
        color: rgba(255,255,255,.5);
        text-decoration: none;
    }
}
@media screen and (max-width: 420px) {
    .navBox {
        left: calc(10% - 14px);
        width: calc(80% + 28px);
        // min-width: 330px;
    }
}
