.container {
    overflow: hidden;
    .title {
        color: #000;
        font-family: microsoft yahei, sans-serif;
        font-size: 16px;
        font-weight: 700;
    }

    .titleMargin {
        margin-top: 6px;
    }

    .btnBox {
        margin-top: 12px;
        text-align: right;
        position: absolute;
        right: 0px;
        bottom: 0px;
    }

    .errorBox {
        width: calc(100% - 100px);
        position: absolute;
        left: 0;
        bottom: 8px;
        color: red;
        font-size: 14px;
        text-align: right;
    }

    .row {
        position: relative;
        height: 50px;
        line-height: 50px;
        border-bottom: 1px solid #eee;
        position: relative;
        font-size: 14px;
        &:hover i{
            transform: rotate(-90deg);
            cursor: pointer;
        }
        i {
            position: absolute;
            right: 12px;
            font-size: 22px;
            color: #555;
            transition: all 0.25s;
            &:active {
                color: #888;
            }
        }

        .templateUrl {
            display: inline-block;
            height: 16px;
            line-height: 16px;
            width: calc(100% - 120px);
            user-select: text;
        }

        .hide {
            opacity: 0;
            position: absolute;
            z-index: -10000;
        }
    }

    label {
        color: #4d4d4d;
        margin-right: 16px;
        width: 64px;
        display: inline-block;
        text-align: right;
    }

    .name {
        width: calc(100% - 80px);
        padding-left: 5px;
        height: 38px;
        border: 0 none;
        outline: none;
    }

    .checkbox {
        position: relative;
        top: 2px;
    }

    .checkboxVal {
        margin-right: 12px;
        font-size: 14px;
    }

    input::-webkit-input-placeholder,
    textarea::-webkit-input-placeholder {
        color: #ccc;
        font-size: 14px;
    }

    input:-moz-placeholder,
    textarea:-moz-placeholder {
        color: #ccc;
        font-size: 14px;
    }

    input::-moz-placeholder,
    textarea::-moz-placeholder {
        color: #ccc;
        font-size: 14px;
    }

    input:-ms-input-placeholder,
    textarea:-ms-input-placeholder {
        color: #ccc;
        font-size: 14px;
    }

    .underline {
        border-bottom: 2px solid var(--theme-color);
        bottom: 0;
        box-sizing: border-box;
        left: 0;
        margin: auto;
        opacity: 0;
        position: absolute;
        right: 0;
        transition: opacity 250ms ease-out, width 0ms linear 180ms;
        width: 0;
    }

    .errorLine {
        @extend .underline;
        border-bottom: 2px solid red;
        opacity: 1;
        transition: width 250ms ease-out, opacity 120ms ease-in;
        width: 100%;
    }

    input:focus + .underline {
      opacity: 1;
      transition: width 250ms ease-out, opacity 120ms ease-in;
      width: 100%;
    }
}
